import Vue from 'vue';
import VueRouter from 'vue-router';
import Login from '../views/Login'
import MyHeader from '../plugin/MyHeader'
import Personal from '../views/Personal_info'
const apply = () => import ('../views/apply')
const comment = () => import ('../views/comment')
const cost =  () => import ('../views/cost')
const dagnjian = () => import ('../views/dagnjian')
const democratic = () => import ('../views/democratic')
const honor = () => import ('../views/honor')
const lesson = () => import ('../views/lesson')
const praty_day  = () => import ('../views/praty_day')
const report = () => import ('../views/report')
const study = () => import ('../views/study')
const voluntary = () => import ('../views/voluntary')
const personal = () => import ('../views/personal')
const dangjianInfo = () => import('../views/dangjianInfo')
const apply_book = () => import('../views/apply_book')

Vue.use(VueRouter)

const routes = [
  {
    path: '/',
    redirect: '/login'
  },
  {
    path: '/login',
    component: Login
  },
  {
    path: '/admin',
    component: MyHeader,
    children: [
      {
        path: '/admin/personal',
        component: Personal
      },
      {
        path: '/',
        redirect: '/admin/personal'
      },
      {
        path: '/admin/dangjian',
        component: apply
      },{
        path: '/admin/comment',
        component: comment
      },{
        path: '/admin/cost',
        component: cost
      },{
        path: '/admin/dagnjian',
        component: dagnjian
      },{
        path: '/admin/democratic',
        component: democratic
      },{
        path: '/admin/honor',
        component: honor
      },{
        path: '/admin/lesson',
        component: lesson
      },{
        path: '/admin/praty_day',
        component: praty_day
      },{
        path: '/admin/report',
        component: report
      },{
        path: '/admin/study',
        component: study
      },{
        path: '/admin/voluntary',
        component: voluntary
      },
      {
        path: '/admin/personal/:id',
        component: personal
      },{
        path: '/admin/dangjian/:id',
        component: dangjianInfo
      },{
        path: '/admin/apply_book/',
        component: apply_book
      },
    ]
  }
]

const router = new VueRouter({
  routes
})
export default router
