<template>
  <div class="login">
    <div class="logo">
      <img src="../assets/logo.png">
      <h2>软件学院党总支智慧党建系统</h2>
    </div>
<!--    <h2>用户登录</h2>-->
    <div class="form">
      <span></span><input type="text" name="uid" v-model="uid" @keyup.tab="getUid" placeholder="账号"><br>
      <span></span><input type="password" name="pwd" v-model="pad" @click="getUid" placeholder="密码"><br>
      <span>{{msg}}</span>
      <div class="but">
        <a href='#' @click="go">注册</a>&nbsp;
        <a href='#' @click="getlogin" @keyup.enter="getUid">登录</a>
      </div>
    </div>
  </div>
</template>

<script>
export default {
  name: "login",
  data () {
    return {
      uid: '',
      pad: '',
      msg: '',
      data: '',
    }
  },
  methods: {
    go (){
      this.$router.push('/admin')
    },
    getUid() {
      let than = this
      than.axios.get('http://127.0.0.1:8000/user?uid='+than.uid).then(function (response){
        if (response.data.length !== 0){
          console.log('ok')
          than.data = response.data[0]
          console.log(response.data)
          // console.log(than.data[0].uid)
        }else{
          than.msg='该账号不存在'
        }
      })
    },
    getlogin (){
      if (this.data.pad === Number(this.pad)){
        console.log('ok')
        this.$router.push('/admin')
      }else{
        console.log(this.data.pad, this.pad)
        this.msg='密码错误'
      }
    }
  }
}
</script>

