<template>
  <div class="personal">
<!--    <a href="#" @click="backHome">返回 </a>-->
    <div class="table table-striped">
      <table class="My_table">
<!--        <tr>-->
<!--          <th width="50px">序号</th>-->
<!--          <th width="75px">姓名</th>-->
<!--          <th width="50px">性别</th>-->
<!--          <th width="200px">民族</th>-->
<!--          <th width="150px">籍贯</th>-->
<!--          <th width="150px">出生日期</th>-->
<!--          <th width="150px">操作</th>-->
<!--        </tr>-->
        <tr>
          <th >序号</th>
          <th >姓名</th>
          <th >性别</th>
          <th >民族</th>
          <th >籍贯</th>
          <th >出生日期</th>
          <th>操作</th>
        </tr>
        <tr v-for="(item,index) in data" :key="index">
          <td height="40px">{{item.id}}</td>
          <td><a href="#" @click="gotoInfo(item.id)">{{item.name}}</a></td>
          <td>{{item.sex?'男':'女'}}</td>
          <td>{{item.nation}}</td>
          <td>{{item.py_ji_guan}}</td>
          <td>{{(item.birth).slice(0,10)}}</td>
          <td><a href="#"><i class="fa fa-pencil-square-o" aria-hidden="true"></i></a><span>&nbsp;||&nbsp;</span>
            <a href="#"><i class="fa fa-trash" aria-hidden="true" @click="deleteInfo(item.id)"></i></a></td>
        </tr>
      </table>
    </div>
    <input type="submit" value="添加党员" @click="showEdit(1)">
    <input type="submit" value="添加培养对象" @click="showEdit(0)">
    <div class="My_edit" id="My_edit">
      <div class="My_form">
        <h2 id="title">添加</h2>
        <div class="inputBx">
          <input type="text" placeholder="姓名" v-model="name">
        </div>
        <div class="inputBx">
          <input type="text" placeholder="性别" v-model="sex">
        </div>
        <div class="inputBx">
          <input type="text" placeholder="民族" v-model="nation">
        </div>
        <div class="inputBx">
          <input type="text" placeholder="籍贯" v-model="py_ji_guan">
        </div>
        <div class="inputBx">
          <input type="text" placeholder="出生日期" v-model="birth">
        </div>
        <div class="inputBx member-1">
          <input type="text" placeholder="入党日期" v-model="date">
        </div>
        <div class="inputBx member-1">
          <input type="text" placeholder="所在支部" v-model="belong">
        </div>
        <div class="inputBx member-1">
          <input type="text" placeholder="职称" v-model="position">
        </div>
        <div class="inputBx member-1">
          <input type="text" placeholder="党内职务" v-model="in_position">
        </div>
        <div class="inputBx member-0">
          <input type="text" placeholder="提交入党申请书日期" v-model="apply_for_date">
        </div>
        <div class="inputBx member-0">
          <input type="text" placeholder="确定入党积极分子日期" v-model="sure_party_date">
        </div>
        <div class="inputBx member-0">
          <input type="text" placeholder="确定发展对象日期" v-model="sure_object_date">
        </div>
        <div class="inputBx member-0">
          <input type="text" placeholder="成为预备党员日期" v-model="already">
        </div>
        <div class="inputBx member-0">
          <input type="text" placeholder="所在支部" v-model="belong">
        </div>
        <div class="inputBx member-0">
          <input type="text" placeholder="培养人一" v-model="teacher_a">
        </div>
        <div class="inputBx member-0">
          <input type="text" placeholder="培养人二" v-model="teacher_b">
        </div>
        <div class="inputBx">
          <input type="submit" value="提交" id="post_sub" @click="post_data">
          <input type="submit" value="提交" id="put_sub" @click="put_data">
          <input type="submit" value="取消" @click="showEdit">
        </div>
      </div>
    </div>
  </div>
</template>

<script>
export default {
  name: "Personal_info",
  data (){
    return {
      msg: '',
      data: '',
      name: '',
      sex: '',
      nation: '',
      py_ji_guan: '',
      birth: '',
      date: '',
      belong: '',
      position: '',
      in_position: '',
      apply_for_date: '',
      sure_party_date: '',
      sure_object_date: '',
      already: '',
      teacher_a: '',
      teacher_b: '',
    }

  },
  methods: {
    backHome(){
      this.$router.push('/login')
    },
    getMsg(){
      var than = this
      this.axios.get('http://127.0.0.1:8000/personal').then(
        (res) => {
          console.log(than)
          console.log(res.data)
          than.data = res.data
        }
      )
    },
    deleteInfo(id){
      const than = this
      this.axios.delete('http://127.0.0.1:8000/personal/'+ id +'/').then((res)=>{
        if (res.status === 204){
          alert('删除成功')
          than.getMsg()
        }
      })
    },
    gotoInfo(id){
      this.$router.push('/admin/personal/'+id+'/')
    },
    showEdit(type){
      if (type === 1){
        const member = document.getElementsByClassName('member-0')
        for (let i=0; i<member.length; i++) {
          member.item(i).style.display = 'none'
        }
        const this_member = document.getElementsByClassName('member-1')
        for (let i = 0; i < this_member.length; i++) {
          // console.log(window.getComputedStyle(this_member.item(i)))
          this_member.item(i).style.display = 'block'
        }
      }else if (type === 0){
        const member = document.getElementsByClassName('member-1')
        for (let i = 0; i < member.length; i++){
          member.item(i).style.display = 'none'
        }
        const this_member = document.getElementsByClassName('member-0')
        for (let i = 0; i < this_member.length; i++) {
          this_member.item(i).style.display = 'block'
        }
      }
      document.getElementById('My_edit').classList.toggle('active')
    },
    post_data() {
    },
    put_data() {

    }
  },
  created () {
    this.getMsg()
  }

}
</script>

